.controls {
  position: absolute;
  bottom: 20px;
  right: 20px;
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in; }
  @media (max-width: 640px) {
    .controls {
      bottom: 70px; } }
  .controls .zoom-in, .controls .zoom-out, .controls .reset {
    padding: 5px;
    margin: 0 3px;
    background-color: #CCE2E6;
    cursor: pointer;
    border-radius: 2px;
    -webkit-transition: all 150ms ease-in-out;
    -o-transition: all 150ms ease-in-out;
    transition: all 150ms ease-in-out; }
    .controls .zoom-in path, .controls .zoom-out path, .controls .reset path {
      fill: #062f4f;
      -webkit-transition: all 150ms ease-in-out;
      -o-transition: all 150ms ease-in-out;
      transition: all 150ms ease-in-out; }
    .controls .zoom-in:hover, .controls .zoom-out:hover, .controls .reset:hover {
      background-color: #062f4f; }
      .controls .zoom-in:hover path, .controls .zoom-out:hover path, .controls .reset:hover path {
        fill: #CCE2E6; }
    .controls .zoom-in:active, .controls .zoom-out:active, .controls .reset:active {
      opacity: 0.8; }
      .controls .zoom-in:active path, .controls .zoom-out:active path, .controls .reset:active path {
        opacity: 0.8; }

.noScroll .controls {
  opacity: 0; }



