.close {
  position: absolute;
  right: -20px;
  top: -20px; }
  .close svg {
    height: 50px;
    fill: rgba(40, 35, 35, 0.8); }
    .close svg:hover {
      fill: #282323; }
  @media (max-width: 640px) {
    .close {
      display: none; } }



