.branding-container {
  position: fixed;
  bottom: 0;
  left: 0;
  padding-bottom: 18vh;
  padding-left: 6vw;
  text-align: center;
  -webkit-transition: all 500ms ease-in-out;
  -o-transition: all 500ms ease-in-out;
  transition: all 500ms ease-in-out; }
  @media (max-width: 1024px) {
    .branding-container {
      padding-bottom: 12vh;
      padding-left: 4vw; } }
  @media (max-width: 640px) {
    .branding-container {
      -webkit-transition-duration: 0ms;
           -o-transition-duration: 0ms;
              transition-duration: 0ms;
      bottom: auto;
      top: 5vh;
      width: 100%; } }

.branding__title {
  padding: 0;
  margin: 0;
  font-size: calc(1.8vh + 1.8vw);
  color: white; }
  @media (max-width: 640px) {
    .branding__title {
      font-size: 30px; } }

.noScroll .branding-container {
  opacity: 0;
  pointer-events: none; }



